html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #121f3d;
  color: #ffffff;
}

p {
  color: #eff3f8;
  line-height: 1.8;
}

.container {
  max-width: 120rem;
  margin: 0 auto;
  padding: 0 2.5rem;
}

.blur {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(18, 31, 61, 0);
  backdrop-filter: blur(8px);
}

.hidden {
  overflow: hidden;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.header {
  display: grid;
  position: fixed;
  width: 100vw;
  height: 10rem;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  z-index: 100;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2.5rem;
}
.header__nav__menu__btn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  padding: 0;
}
.header__nav__menu__btn svg {
  width: 5rem;
  z-index: 100;
}
.header__nav__menu__btn--line {
  fill: none;
  stroke: #ffffff;
  stroke-width: 6;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header__nav__menu__btn--line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.header__nav__menu__btn--line2 {
  stroke-dasharray: 60 60;
  stroke-width: 6;
}
.header__nav__menu__btn--line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.opened .header__nav__menu__btn--line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
.opened .header__nav__menu__btn--line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6;
}
.opened .header__nav__menu__btn--line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
.header__nav__menu__aside {
  display: grid;
  place-items: center;
  height: 100vh;
  width: 0;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #03091e;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}
.header__nav__menu__aside.opened {
  width: min(75vw, 40rem);
}
.header__nav__menu__aside__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
}
.header__nav__menu__aside__nav a {
  color: #eff3f8;
}
.header__nav__menu__aside__nav--resume {
  margin: 10% auto 0px;
  padding: 18px 50px;
  color: #0693e3;
  background-color: transparent;
  border: 2px solid #0693e3;
  border-radius: 2.375rem;
  font-size: 1.3rem;
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}

.down,
.up {
  height: 70px;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.down {
  transform: translateY(-70px);
}

.up {
  transform: translateY(0px);
  box-shadow: 0 10px 30px -10px rgba(2, 12, 27, 0.7);
}

.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}
.hero__content h2 {
  color: #33b1ff;
}
.hero__content h4,
.hero__content h2 {
  margin-bottom: 2rem;
}
.hero__content p {
  margin: 2rem 0 0 0;
  font-size: 1.4rem;
}
.hero__btn {
  width: 15rem;
  margin-top: 5rem;
  padding: 1.25rem 1.75rem;
  text-align: center;
  color: #0693e3;
  background-color: transparent;
  border: 2px solid #0693e3;
  border-radius: 2.375rem;
  font-size: 1.3rem;
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}

.about__skillList {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin: 2rem 0;
  padding: 0;
  list-style: none;
}
.about__skillList li {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 2rem;
}
.about__skillList li::before {
  content: "▹";
  position: absolute;
  left: 0px;
  color: #33b1ff;
  font-size: 1.3rem;
  line-height: 12px;
}
.about__img {
  width: 70%;
  margin: 6rem auto;
  position: relative;
}
.about__img img {
  border-radius: 1rem;
}
.about__img::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 2rem;
  left: 2rem;
  border: 2px solid #0693e3;
  border-radius: 1rem;
  z-index: -1;
}

.projects h2 {
  margin: 1rem 0 4rem;
  font-size: 2.6rem;
}
.projects__list__content {
  margin-bottom: 3rem;
  padding: 2.5rem;
  background-color: #243858;
  box-shadow: 0 10px 30px -15px rgba(2, 12, 27, 0.7);
}
.projects__list__content p {
  margin: 0 0 1rem 0;
}
.projects__list__content h3 {
  margin-bottom: 3rem;
}
.projects__list__content__techList {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin: 2rem 0;
  padding: 0;
  font-weight: 300;
  list-style: none;
}
.projects__list__content__links {
  display: flex;
  justify-content: flex-start;
  gap: 2rem;
}

.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10rem 0;
}
.contact h2 {
  text-align: center;
}
.contact p {
  font-size: 1.4rem;
  text-align: center;
}
.contact__email {
  padding: 1.25rem 1.75rem;
  margin-top: 5rem;
  color: #0693e3;
  background-color: transparent;
  border: 2px solid #0693e3;
  border-radius: 2.375rem;
  line-height: 1;
  font-size: 1.3rem;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}

.footer {
  display: grid;
  place-items: center;
  padding: 1.5rem;
}
.footer__socialLinks {
  display: flex;
  gap: 4rem;
}
.footer__made {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 1.4rem;
}
.footer__credit {
  font-size: 1rem;
}
.footer__credit a {
  color: inherit;
}